import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

const imgUrl = new URL('src/assets/models/globe/globe.glb', import.meta.url).href;

export class Globe {
  loader: GLTFLoader;
  onLoadProgress: (event: ProgressEvent<EventTarget>) => void;
  onLoadError: (error: unknown) => void;

  constructor(onLoadProgress: (event: ProgressEvent<EventTarget>) => void, onLoadError: (error: unknown) => void) {
    this.loader = new GLTFLoader();
    this.loader.setRequestHeader({ Accept: 'application/gltf-buffer,application/json,application/octet-stream,*/*' });

    this.onLoadProgress = onLoadProgress;
    this.onLoadError = onLoadError;
  }

  async init() {
    const globeData = await this.loader.loadAsync(imgUrl, this.onLoadProgress).catch(this.onLoadError);
    if (!globeData) {
      throw new Error('Failed to retrieve globe data!');
    }

    const model = globeData.scene ?? globeData.scenes[0];
    if (!model) {
      console.error(globeData);
      throw new Error('Globe data malformed!');
    } else {
      return model;
    }
  }
}
