import { PerspectiveCamera, Scene, WebGLRenderer } from 'three';
import { Globe } from './GlobeEntity';

export class GlobeScene {
  private mountEl: HTMLElement;

  private globe: Globe;

  private scene: Scene;
  private camera: PerspectiveCamera;
  private renderer: WebGLRenderer;

  private _destroyed: boolean = false;

  constructor(
    mountEl: HTMLElement,
    height: number,
    width: number,
    onLoadProgress: (event: ProgressEvent<EventTarget>) => void,
    onLoadError: (error: unknown) => void,
  ) {
    this.mountEl = mountEl;

    this.globe = new Globe(onLoadProgress, onLoadError);

    this.scene = new Scene();
    this.camera = new PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);
    this.renderer = new WebGLRenderer();
    this.renderer.setSize(width, height);
  }

  async init() {
    const globeModel = await this.globe.init();

    if (!this._destroyed) {
      this.mountEl.appendChild(this.renderer.domElement);

      this.scene.add(globeModel);

      this.camera.position.z = 5;

      this.renderer.render(this.scene, this.camera);

      this.animate();
    }
  }

  private animate() {
    requestAnimationFrame(() => this.animate());
    this.renderer.render(this.scene, this.camera);
  }

  async destroy() {
    this._destroyed = true;
    try {
      this.scene.remove(...this.scene.children);
      this.renderer.dispose();
      this.mountEl.removeChild(this.renderer.domElement);
    } catch (e) {
      // noop
    }
  }
}
